import { Container, Button, Nav, CardGroup, Card, Spinner, Alert, Table } from "react-bootstrap";
import { useSpring, animated } from "react-spring";
import PropTypes from "prop-types";
import { connect } from "react-redux";
import React, { useState, useEffect } from "react";
import axios from "axios";

import menuRedHighlight from "./images/menuRedHighlight.png";
import menuItemSelect from "./images/menuItemSelect.png";
import addToBag from "./images/addToBag.png"
import languageBox from "./images/languageBox.png";
import selectLanguage from "./images/selectLanguage.png";
import saveSettings from "./images/saveSettings.png";

import cashierSubmit from "./images/cashierSubmit.png";
import cashierAdd from "./images/cashierAdd.png";
import cashierEditItem from "./images/cashierEditItem.png";

import "./tableStyle.css";

function Help(props) {

  const [isLoading, setLoading] = useState([]);
  const [message, setMessage] = useState();

  useEffect(() => {

    setLoading(false);

  }, []);

  return (
  
      <div className="Help">
        {isLoading ? (
          <div className="d-flex justify-content-center">
            <Spinner animation="border" />
          </div>
        ) : (
          <React.Fragment>
            <h2 className="display-3 text-center"> Help </h2>
            <div className="d-flex justify-content-center">
                <p><i>Click on an item to learn more about that topic!</i></p>
            </div>
            <br></br>

            {message && (
              <React.Fragment>
                <Alert className="text-center" variant="danger">
                  {message}
                </Alert>
                <br></br>
              </React.Fragment>
            )}

            {props.auth.user.role === "online-user" && 
                <React.Fragment>
                    

                </React.Fragment>
            }

            {props.auth.user.role === "user" && 
                <React.Fragment>
                    <Table bordered hover className="text-center">
                        <tbody>
                            <tr className="table-secondary" data-toggle="collapse" data-target=".commentRow0" style={{cursor:'pointer'}}>
                                <td class="col-md-2">How to Make an Order </td>
                            </tr>
                            <tr>
                                <td className="hiddenRow"><div className="collapse commentRow0">
                                    <br></br>
                                        <p>1. Click the box for whatever item you wish to add to your bag.</p>
                                        <p>2. Select item details and click add to bag.</p>
                                        <p>3. Click 'Bag' in the navigation menu up top.</p>
                                        <p>4. Confirm your order and click 'Submit Order'.</p>
                                        <p>5. Wait by the pickup counter for your food.</p>

                                    <br></br>
                                </div></td>
                            </tr>
                            <tr className="table-secondary" data-toggle="collapse" data-target=".commentRow1" style={{cursor:'pointer'}}>
                                <td class="col-md-2">How to Add Items to Your Bag </td>
                            </tr>
                            <tr>
                                <td className="hiddenRow"><div className="collapse commentRow1">
                                    <br></br>
                                        <p>1. Click the box for whatever item you wish to add to your bag. (Boxes highlighted in red)</p>
                                        <img src={menuRedHighlight} width="500px"></img>
                                        <br></br><br></br>
                                        <p>2. Select desired options from the menu.</p>
                                        <img src={menuItemSelect} width="300px"></img>
                                        <br></br><br></br>
                                        <p>3. Click 'Add to Bag'</p>
                                        <img src={addToBag} width="500px"></img>
                                        <br></br><br></br>
                                    <br></br>
                                </div></td>
                            </tr>

                            <tr className="table-secondary" data-toggle="collapse" data-target=".commentRow2" style={{cursor:'pointer'}}>
                                <td class="col-md-2">How to Edit an Order </td>
                            </tr>
                            <tr>
                                <td className="hiddenRow"><div className="collapse commentRow2">
                                    
                                    Sup
                                    
                                </div></td>
                            </tr>

                            <tr className="table-secondary" data-toggle="collapse" data-target=".commentRow3" style={{cursor:'pointer'}}>
                                <td class="col-md-2">Submitting an Order </td>
                            </tr>
                            <tr>
                                <td className="hiddenRow"><div className="collapse commentRow3">
                                    
                                    Sup
                                    
                                </div></td>
                            </tr>

                            <tr className="table-secondary" data-toggle="collapse" data-target=".commentRow4" style={{cursor:'pointer'}}>
                                <td class="col-md-2">Changing your Settings </td>
                            </tr>
                            <tr>
                                <td className="hiddenRow"><div className="collapse commentRow4">
                                    <p>1. Navigate to the 'Settings' tab in the navigation above.</p>
                                    <p>As of now, we only support language changes.</p>
                                    <p>2. Select the language drop down box and pick your desired language.</p>
                                    <img src={languageBox} width="800px"></img>
                                    <br></br><br></br>
                                    <img src={selectLanguage} width="500px"></img>
                                    <br></br><br></br>
                                    <img src={saveSettings} width="500px"></img>
                                    <br></br><br></br>
                                    <p>3. Wait for new language to load.</p>
                                    <br></br><br></br>
                                </div></td>
                            </tr>
                        </tbody>

                    </Table>
                </React.Fragment>
            }

            {props.auth.user.role === "cashier" && 
                <React.Fragment>
                    <Table bordered hover className="text-center">
                        <tbody>
                            <tr className="table-secondary" data-toggle="collapse" data-target=".commentRow1" style={{cursor:'pointer'}}>
                                <td class="col-md-2">What to do when a customer approaches </td>
                            </tr>
                            <tr>
                                <td className="hiddenRow"><div className="collapse commentRow1">
                                    <p>1. Greet the customer and look at their meal.</p>   
                                    <p>2. Enter in click buttons to add an item to their receipt.</p>
                                    <img src={cashierAdd}></img>
                                    <br></br><br></br>
                                    <p>3. Tell the customer their total and ask for their payment.</p>
                                    <p>4. Process the customer's payment and give them their meal.</p>
                                </div></td>
                            </tr>

                            <tr className="table-secondary" data-toggle="collapse" data-target=".commentRow2" style={{cursor:'pointer'}}>
                                <td class="col-md-2">How to Edit an Order </td>
                            </tr>
                            <tr>
                                <td className="hiddenRow"><div className="collapse commentRow2">
                                    <p>1. If you added an item by mistake or the customer changed their mind, select the 'X' next to the item.</p>
                                    <img src={cashierEditItem}></img>
                                    <br></br><br></br>
                                    <p>2. If needed, add another item to the receipt before continuing.</p>
                                </div></td>
                            </tr>

                            <tr className="table-secondary" data-toggle="collapse" data-target=".commentRow3" style={{cursor:'pointer'}}>
                                <td class="col-md-2">Submitting an Order </td>
                            </tr>
                            <tr>
                                <td className="hiddenRow"><div className="collapse commentRow3">
                                    <p>1. Confirm that the receipt is correct with the customer.</p>
                                    <p>2. Select 'Submit Order' below the items in the receipt.</p>
                                    <img src={cashierSubmit}></img>
                                    <br></br><br></br>
                                    <p>3. Give the customer their food and say you salutation.</p>
                                </div></td>
                            </tr>

                            <tr className="table-secondary" data-toggle="collapse" data-target=".commentRow4" style={{cursor:'pointer'}}>
                                <td class="col-md-2">Changing your Settings </td>
                            </tr>
                            <tr>
                                <td className="hiddenRow"><div className="collapse commentRow4">
                                    <p>1. Navigate to the 'Settings' tab in the navigation above.</p>
                                    <p>As of now, we only support language changes.</p>
                                    <p>2. Select the language drop down box and pick your desired language.</p>
                                    <img src={languageBox} width="800px"></img>
                                    <br></br><br></br>
                                    <img src={selectLanguage} width="500px"></img>
                                    <br></br><br></br>
                                    <img src={saveSettings} width="500px"></img>
                                    <br></br><br></br>
                                    <p>3. Wait for new language to load.</p>
                                    <br></br><br></br>
                                </div></td>
                            </tr>
                        </tbody>

                    </Table>

                </React.Fragment>
            }

            {props.auth.user.role === "manager" && 
                <React.Fragment>
                    <Table bordered hover className="text-center">
                        <tbody>
                            <tr className="table-secondary" data-toggle="collapse" data-target=".commentRow1" style={{cursor:'pointer'}}>
                                <td class="col-md-2">How to Process Orders </td>
                            </tr>
                            <tr>
                                <td className="hiddenRow"><div className="collapse commentRow1">
                                    <p>1. Greet the customer and look at their meal.</p>   
                                    <p>2. Enter in click buttons to add an item to their receipt.</p>
                                    <img src={cashierAdd}></img>
                                    <br></br><br></br>
                                    <p>3. Confirm that the receipt is correct with the customer.</p>
                                    <p>4. Select 'Submit Order' below the items in the receipt.</p>
                                    <img src={cashierSubmit}></img>
                                    <br></br><br></br>
                                    <p>5. Give the customer their food and say you salutation.</p>

                                    <br></br>
                                    <p><b>If you need to edit an item:</b></p>
                                    <p>1. If you added an item by mistake or the customer changed their mind, select the 'X' next to the item.</p>
                                    <img src={cashierEditItem}></img>
                                    <br></br><br></br>
                                    <p>2. If needed, add another item to the receipt before continuing.</p>
                                </div></td>
                            </tr>

                            <tr className="table-secondary" data-toggle="collapse" data-target=".commentRow2" style={{cursor:'pointer'}}>
                                <td class="col-md-2">How to Edit Menu Items </td>
                            </tr>
                            <tr>
                                <td className="hiddenRow"><div className="collapse commentRow2">Sup</div></td>
                            </tr>

                            <tr className="table-secondary" data-toggle="collapse" data-target=".commentRow3" style={{cursor:'pointer'}}>
                                <td class="col-md-2">How to Add New Menu Items </td>
                            </tr>
                            <tr>
                                <td className="hiddenRow"><div className="collapse commentRow3">Sup</div></td>
                            </tr>

                            <tr className="table-secondary" data-toggle="collapse" data-target=".commentRow4" style={{cursor:'pointer'}}>
                                <td class="col-md-2">How to Delete Menu Items </td>
                            </tr>
                            <tr>
                                <td className="hiddenRow"><div className="collapse commentRow4">Sup</div></td>
                            </tr>

                            <tr className="table-secondary" data-toggle="collapse" data-target=".commentRow5" style={{cursor:'pointer'}}>
                                <td class="col-md-2">How to Generate Reports </td>
                            </tr>
                            <tr>
                                <td className="hiddenRow"><div className="collapse commentRow5">Sup</div></td>
                            </tr>

                            <tr className="table-secondary" data-toggle="collapse" data-target=".commentRow6" style={{cursor:'pointer'}}>
                                <td class="col-md-2">Changing your Settings </td>
                            </tr>
                            <tr>
                                <td className="hiddenRow"><div className="collapse commentRow6">
                                    <p>1. Navigate to the 'Settings' tab in the navigation above.</p>
                                    <p>As of now, we only support language changes.</p>
                                    <p>2. Select the language drop down box and pick your desired language.</p>
                                    <img src={languageBox} width="800px"></img>
                                    <br></br><br></br>
                                    <img src={selectLanguage} width="500px"></img>
                                    <br></br><br></br>
                                    <img src={saveSettings} width="500px"></img>
                                    <br></br><br></br>
                                    <p>3. Wait for new language to load.</p>
                                    <br></br><br></br>
                                </div></td>
                            </tr>
                        </tbody>

                    </Table>

                </React.Fragment>
            }


            
            <br></br><br></br><br></br><br></br>
          </React.Fragment>
      )
    }
    </div>
    );


}

Help.propTypes = {
  auth: PropTypes.object.isRequired,
};

const mapStateToProps = (state) => ({
  auth: state.auth,
});

export default connect(mapStateToProps)(Help);